<template>
  <div class="ballanimation">
    <div style="text-align:center">
      <button class="layui-btn layui-btn-primary layui-border" @click="isStartChange" v-if="!isBallStart" id="start" title="休闲益智小游戏:碰撞球">休闲益智小游戏:碰撞球</button><br>
      <span style="color: #FFFFFF" name="tips" title="鼠标左键放置,双击重置;鼠标右键退出">鼠标左键放置,双击重置;鼠标右键退出</span><br>
      <span style="font-size: 1px; color: #FFFFFF" title="非电脑用户退出请刷新页面">非电脑用户退出请刷新页面</span>
    </div>
    <div id="canvas"></div>
  </div>
</template>


<script>
import protoclass from './js/protoclass'
import box2d from './js/box2d'
import boxmain from './js/Main'

export default {
  name: 'ballAnimation',
  data() {
    return {
      isBallStart: false,
    }
  },
  methods: {
    isStartChange() {
      this.isBallStart = !this.isBallStart
      boxmain(this.isBallStart)
      this.isBallStart = !this.isBallStart
    },
    isExitGame() {
      document.oncontextmenu = none;
    }
  },
  created() {
    document.body.className = 'body'
  },
  mounted() {
    protoclass()
    box2d()
    boxmain(this.isBallStart)
  }
}

</script>

